/* Header.css */

body {
  font-family: 'Pretendard Variable';
  font-size: 14px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: none;
  -ms-overflow-style: none;
  margin: 0;
  overflow-y: scroll;
}

#root {
  width: 100vw;
  max-width: 480px;
  height: 100vh;
  margin: 0 auto;
  overflow-x: hidden;
}

/* 모든 스크롤바 숨기기 (크롬, 파이어폭스, Safari) */
::-webkit-scrollbar {
  display: none;
}

.flex {
  display: flex;
}

.mt-100 {
  margin-top: 100px !important;
}

.mb-100 {
  margin-bottom: 100px !important;
}

.my-0 {
  margin-top: 0;
  margin-bottom: 0;
}

.text-center {
  text-align: center;
}

.bg-grey {
  background-color: #EDEDED;
}

.bg-ygreen {
  background-color: #B3CE61;
}

.bg-green {
  background-color: #28A7A1;
}

bg- .mobile-wrap {
  overflow-y: scroll;
  height: 100vh;
  max-width: 480px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  /* 세로 중앙 정렬 */
  justify-content: center;
}

/* header */
header {
  position: relative;
  width: 100%;
  height: 62px;
  background-color: #28A7A1;
  border-radius: 0 0 30px 30px;
}

.header-logo img {
  max-height: 44.5px;
}

.menu-container {
  position: absolute;
  width: 285px;
  right: -480px;
  background-color: white;
  padding: 25px;
  border: 1px solid #ccc;
  transition: right 0.3s ease;
  z-index: 11;
}

.menu-container.visible {
  right: 0;
}

.menu-container div {
  font-weight: bold;
  margin-bottom: 10px;
}

.menu-container ul {
  padding: 0;
  margin: 0;
}

.menu-container li {
  cursor: pointer;
  font-size: 16px;
  padding: 20px 0;
  list-style: none;
}

.menu-container li span {
  float: right;
}

.menu-container div strong {
  float: right;
  cursor: pointer;
}

.header-wrap {
  position: absolute;
  display: grid;
  grid-template-columns: 20% 60% 20%;
  width: 100%;
  max-width: 480px;
  text-align: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
}

.menu-button {
  cursor: pointer;
}

/* loading */
.loading-page {
  background-color: #717171;
  text-align: center;
  color: white;
  font-size: 3.5vw;
}

.loading-page img {
  max-width: 160px;
  width: 30vw;
}

@media (min-width: 481px) {
  .loading-page {
    font-size: 15px;
  }
}

/* index */
.main_bg {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: #84D6ED;
}

.main_map {
  position: absolute;
  top: 62px;
  height: calc(100vh - 62px);
  width: 100%;
  max-width: 480px;
  overflow-y: scroll;
  margin: 0 auto;
}

.main_map p {
  text-align: center;
  font-size: 35px;
  color: white;
  margin: 0;
  position: fixed;
  width: 100%;
  left: 50%;
  top: 17%;
  transform: translate(-50%, -50%);
}

.main_map strong {
  font-weight: 900;
}

.main_map img {
  position: fixed;
  width: 520px;
  left: 48%;
  top: 55%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

@media (max-width: 380px) {
  .main_map img {
    width: 450px;
    top: 60%;
  }
}

/* Join */
.join {
  max-width: 350px;
  font-size: 14px;
  margin: 0 auto;
  padding-top: 30px;
  overflow-x: hidden;
}

.join h1 {
  font-size: 16px;
  text-align: center;
  border-bottom: 1px solid #D0D0D0;
  padding-bottom: 10px;
}

.join h1 a {
  float: left;
  cursor: pointer;
  text-decoration: none;
  color: #1C1B1B
}

.join h2 {
  text-align: center;
  background-color: #EEEEEE;
  color: #999999;
  font-size: 11px;
  font-weight: 400;
  padding: 10px;
  border-radius: 5px;
}

.join input,
.join select {
  max-width: 350px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #E2E2E2;
  color: #676767;
}

.birth_wrap {
  max-width: 350px;
  display: flex;
  justify-content: space-around;
}

.birth_wrap input {
  width: 30%;
  flex: 0 0 30%;
  color: #676767;
}

#joinCheck {
  background-color: #9C9C9C;
  color: white;
  border: none;
  font-size: 11px;
  padding: 5px;
  width: 100%;
  flex: 0 0 60px;
}

.join input[type="checkbox"] {
  max-width: 15px;
  float: left;
}

#btnJoin {
  width: 100%;
  background-color: #28A7A1;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  max-width: 350px;
  margin: 30px auto;
  font-weight: bold;
}

.join .checkList {
  margin-bottom: 5px;
}

/* 프로필 */
.profile {
  max-width: 300px;
  margin: 0 auto;
}

.profile h1 {
  font-size: 22px;
  font-weight: 400;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 5px;
}

.profile h4 {
  font-size: 12px;
  font-weight: 400;
  text-align: center;
  margin-top: 0px;
  margin-bottom: 20px;
}

.profile h3 {
  font-size: 18px;
  font-weight: 400;
  margin-top: 30px;
  margin-bottom: 10px;
}

.profileOption {
  background-color: #C0C0C0;
  color: white;
  padding: 3px 15px;
  margin-right: 15px;
  margin-bottom: 10px;
  border-radius: 10px;
  width: fit-content;
  display: inline-block;
}

.profileOption.active {
  background-color: #CCCE61;
  color: white;
}

.profileBtn {
  width: 100%;
  background-color: #CCCE61;
  border: none;
  margin-top: 150px;
  padding: 5px;
  font-size: 16px;
  color: white;
}

#gohomeBtn {
  border: none;
  background-color: #C5C5C5;
  color: white;
  font-size: 14px;
  padding: 10px 30px;
  text-decoration: none;
}

.linkHome {
  border-radius: 10px;
  margin-top: 100px;
  padding: 30px 50px;
}

.linkHome h4 {
  margin-top: 10px;
  font-size: 9px;
}

.linkHome p {
  margin-top: 35px;
}

.whBtn {
  font-weight: 500;
  border-radius: 10px;
  font-size: 14px;
  text-decoration: none;
  color: black;
  background: white;
  padding: 5px 25px;
}

.headerWrap {
  padding: 20px 35px;
}

.headerTxt {
  display: inline-block;
  margin: 0;
  font-size: 24px;
  font-weight: bold;
}

.btnWrap {
  float: right;
}

.btnWrap button {
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  color: white;
  font-size: 19px;
  font-weight: 600;
  border-radius: 10px;
  margin-left: 10px;
}

hr {
  background-color: #CFCFCF;
  height: 0.5px;
  border: none;
}

.filterPopup,
.sortPopup,
.postProfileBG {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.2);
  top: 0;
  left: 0;
  display: none;
}

.filterPopupWrap,
.sortWrap,
.postProfileWrap {
  position: absolute;
  top: 0;
  left: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.filterList {
  box-sizing: border-box;
  padding: 35px;
  background-color: white;
  width: 90vw;
  margin: 0;
  height: fit-content;
  border-radius: 15px;
}

#filterApply {
  width: 90vw;
  background-color: #B3CE61;
  font-weight: bold;
  color: white;
  font-size: 16px;
  padding: 10px;
  margin-top: 15px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
}

@media (min-width: 341px) {

  .filterList, #filterApply{
    width: 330px;
  }
}

.filterList h4 {
  margin: 10px 0;
}

.filterList h1 {
  margin-top: 0;
  margin-bottom: 30px;
}

.filterList h1 span {
  float: right;
  cursor: pointer;
}

.filterList ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.filterList ul li {
  width: 50px;
  text-align: center;
  cursor: pointer;
  font-size: 11px;
  display: inline-block;
  background-color: #DBDBDB;
  padding: 5px 0;
  border-radius: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
  font-weight: 500;
}

.filterList ul li.active {
  background-color: #B3CE61;
  font-weight: bold;
  color: white;
}

.react-datepicker-wrapper input {
  padding: 5px 0;
  text-align: center;
  background-color: #DBDBDB;
  font-weight: bold;
  border: none;
  border-radius: 15px;
  font-size: 11px;
  cursor: pointer;
}

.react-datepicker-wrapper input::placeholder {
  font-size: 10px;
  color: black;
  font-weight: 500;
}

.filterWrap ul {
  cursor: pointer;
  padding-left: 5%;
}

.filterWrap ul li {
  display: inline-block;
  padding: 5px 0;
  margin-right: 10px;
  font-size: 16px;
}

.filterWrap ul li span {
  padding-left: 5px;
  color: #A9A9A9;
}

.sortBtn {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 3%;
  color: #9A9A9A;
  cursor: pointer;
}

.sortBtn img {
  margin-right: 4px;
  vertical-align: middle;
}

.postList {
  width: 100%;
  padding: 0;
  margin-bottom: 0;
}

.postList li {
  list-style: none;
  border-bottom: 1px solid #D0D0D0;
  padding-left: 3%;
  padding-right: 3%;
  padding-bottom: 10px;
  cursor: pointer;
}

.postList li h2 {
  font-size: 16px;
  color: #1C1B1B;
}

.postList li p {
  margin: 10px 0;
  font-size: 16px;
  color: #9A9A9A;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.postList li span strong {
  font-weight: 400;
  padding: 3%;
}

.postList .postFilter {
  padding: 0;
}

.postList .postFilter li {
  display: inline-block;
  background-color: #D9D9D9;
  font-size: 11px;
  border-radius: 5px;
  padding: 2.5px 15px;
  margin-right: 15px;
}

.sortList {
  background-color: white;
  width: 285px;
  border-radius: 15px;
  text-align: center;
  list-style: none;
  margin-top: 0;
  padding: 30px 0;
}

.sortList li {
  width: 220px;
  margin: 0 auto;
  font-size: 11px;
  font-weight: 500;
  margin-bottom: 10px;
  padding: 0;
  cursor: pointer;
}

.sortList li:nth-child(2) {
  border-top: 1px solid #DCDCDC;
  border-bottom: 1px solid #dcdcdc;
  padding-top: 10px;
  padding-bottom: 10px;
}

.cancleBtn {
  font-size: 11px;
  font-weight: bold;
  background-color: #646464;
  width: 100%;
  border-radius: 30px;
  border: none;
  padding: 10px;
  color: white;
  cursor: pointer;
}

/* postDetail */
.postDetail {
  padding: 0 5%;
  font-size: 16px;
}

.postDetail h2 {
  font-size: 16px;
  font-weight: 500;
}

.postDetail p{
  white-space: pre-line;
}

.DetailHeader{
  color: #9A9A9A;
  display: flex;
  align-items: center;
}

.DetailProfileImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #D9D9D9;
  cursor: pointer;
}
.DetailNickname{
  padding-left: 10px;
  flex:40%;
}

.DetailDateNHit {
  color: #9A9A9A;
}

.DetailFilterWrap ul{
  padding:0;
  margin:20px 0 50px 0;
}

.DetailFilterWrap ul li{
  list-style: none;
  display: inline-block;
  color: #28A7A1;
  background-color: #F0F0F0;
  padding:5px 10px;
  margin-right:5px;
}

#writePostBtn{
  background-color: #B3CE61;
  font-weight: bold;
  font-size: 16px;
  color:white;
  border:none;
  width:100%;
  padding:10px;
  border-radius: 30px;
  position: relative;
  bottom:10%;
}

.postProfileWrap{
  background-color: white;
  width: 230px;
  padding:15px 20px;
  border-radius: 5px;
}

.postProfileWrap h1{
  font-size: 16px;
}

.postReport{
  color:#979797;
  float: right;
  border-bottom: 1px solid #979797;
  width: fit-content;
}

.postProfileImg{
  margin-top:50px;
  text-align: center;
}

.postNickname{
  text-align: center;
}

.postProfileFilter ul{
  padding:0;
  margin:15px 0;
}

.postProfileFilter ul li{
  list-style: none;
  display: inline-block;
  background-color: #B3CE61;
  color:white;
  width: 54px;
  border-radius: 10px;
  text-align: center;
  padding:5px 0;
  margin-right: 3.5px;
  margin-bottom: 10px;
}

.postProfileInfo{
  border-radius: 5px;
  background-color: #F2F2F2;
  font-weight: 500;
  font-size: 10px;
  padding:10px 15px;
  line-height: 1.5;
  min-height: 110px;
  margin-bottom: 50px;
}

.postBackBtn{
  background-color: #676767;
  color: white;
  font-weight: 500;
  font-size: 10px;
  padding:10px 0;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
}